body {
  background-color: lightblue;

}

.container {
  display: flex;
  justify-content: space-around;
  margin-top: 75px;
}

#game_container {
  position: relative;
}

canvas {
  opacity: 1;
  position: absolute;
  left: -300px;
}

#background {
  width: 600px;
  height: 800px;
  position: absolute;
  left: -300px;
  // left: 90px;
  // position: relative;
  // z-index: -1;
}

#scoreboard {
  padding: 5px;
  position: absolute;
  border: 2px solid yellowgreen;
  box-shadow: 0 0 10px #31b43a;
  margin-top: 300px;
  text-align: center;
  margin-right: 50px;
}

.pressed {
  background-color: 4px solid #4dcc7d;
  color: #4dcc7d;
  box-shadow: 0 0 10px #31b43a;
}

.right div:last-child {
  display: block;
  margin-top: 490px;
}

.left div + div {
  margin-top: 600px;
}

button:hover {
  background-color: 4px solid #4dcc7d;
  color: #4dcc7d;
  box-shadow: 0 0 10px #31b43a;
}

.start {
  position: absolute;
  margin-top: 300px;
  text-align: center;
  margin-right: 85px;
  padding: 15px;
  background: rgb(238, 174, 202);
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
}
.start button {
  padding: 7px;
  margin: 10px;
}
div + p {
  font-family: "Dank Mono", ui-monospace, monospace;
}

#voice_img {
  padding-top: 125px;
  padding-left: 350px;
}

#scoreboard p {
  color: aliceblue;
  text-decoration: underline;
  font-weight: bolder;
}

.start {
  border: 2px solid black;
}
